<template>
  <div class="app-container" id="deviceProxy">
    <div class="filter-container">
      <el-button class="filter-item" type="primary" @click="handleCreate" v-if="power==1" icon="el-icon-circle-plus-outline">注册</el-button>
    </div>
    <div class="tableBox">
      <el-table :key="tableKey" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName">
        <el-table-column label="序号" prop="id" fixed sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
        <el-table-column label="设备号" min-width="200px" align="center">
          <template slot-scope="{row}">
            <span >{{ row.imei }}</span>
          </template>
        </el-table-column>
         <el-table-column label="代理设备号" min-width="200px" align="center">
          <template slot-scope="{row}">
            <span >{{ row.serialNo }}</span>
          </template>
        </el-table-column>
          <el-table-column label="代理车牌号" min-width="200px" align="center">
          <template slot-scope="{row}">
            <span >{{  $store.state.realTimeMonitor.carAndGunObj[row.serialNo]? $store.state.realTimeMonitor.carAndGunObj[row.serialNo].code:row.serialNo}}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center" min-width="500" fixed="right" class-name="small-padding fixed-width" v-if="power==1">
          <template slot-scope="{row}">
            <el-button type="primary" size="mini" @click="handleBind(row)" icon="iconfont iconsuo">绑定</el-button>
          </template>
        </el-table-column>  
      </el-table>
    </div>

    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />

    <el-dialog id="myDialog" @close="scrollTop" :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible" center :class="{'editClass':true}">
      <el-form ref="dataForm" :rules="rules" :model="temp" label-position="left" class="formStyle">
        <el-form-item label="设备号" prop="imei" v-if="dialogStatus=='create'">
          <el-input v-model="temp.imei" />
        </el-form-item>

     
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="createData()">确定</el-button>
      </div>
    </el-dialog>
 
    <el-dialog id="myDialog" :title="bindMap[bindDialogStatus]" :visible.sync="BindDialogFormVisible" center>
      <el-form ref="bindForm" :rules="bindRules" :model="bindTemp" label-position="left" class="formStyle">
  <el-form-item label="绑定车辆" prop="carId" >
          <el-select filterable v-model="bindTemp.carId " class="filter-item" placeholder="Please select">
            <el-option v-for="item in carOptions" :key="item.serialNo" :label="item.licenseNo" :value="item.serialNo" />
          </el-select>
        </el-form-item>
      
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="bindSubmit()">确定</el-button>
      </div>
    </el-dialog>
   
   
    
  </div>
</template>

<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination

//车辆列表，添加车辆，编辑，删除，搜索，批量添加车辆
import {
  proxycarinfoReg,
  proxycarinfoBind,
  proxycarinfoList
} from "../../../api/managementArchives/deviceProxy";


import { teamAndCarAllBindList } from "../../../api/realTimeMonitoring";


//未选择
const unSelect = (rule, value, callback) => {
  if (value == -1) {
    callback(new Error("请选择"));
  } else {
    callback();
  }
};
let myLoading = undefined; // 弹窗服务调用
export default {
  name: "deviceProxy",
  components: {
    Pagination
  },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限

      tableKey: 0,
      list: null, //车辆列表
      total: 0, //车辆总数
      listLoading: true,
      indexMethod: 1, // 序号
      listQuery: {
        page: 1,
        limit: 10
      },
   

      channelNoOptions: [], //通道个数下拉数据
      //添加或编辑数据
      temp: {
       
      },
      dialogFormVisible: false, //添加或编辑窗口显隐
      dialogStatus: "", //控制是编辑窗口还是添加窗口
      textMap: {
        create: "注册"
      },
      //添加或编辑表单校验
      rules: {
        imei: [
          {
            required: true,
            message: "请填写设备号",
            trigger: "blur"
          }
        ]
      },
   
      bindTemp: {

      },

      BindDialogFormVisible: false,
      bindDialogStatus: "",//控制是绑定还是解绑窗口
      bindMap: {
        bind: "绑定",
        unbind: "解绑"
      },
      bindRules: {
        carId: [
          { required: true, message: "请选择车辆", trigger: "change", validator: unSelect }
        ]
      },
      carId: null,
      carOptions:[],

    };
  },
  created() {
    this.getList(); //初始化获取车辆列表
    this.getSelect(); //初始化弹窗的下拉框
    this.getChannelNoOptions(); //初始化通道下拉数据
  },
  mounted() { },
  methods: {
    handleBind(row) {
      this.bindDialogStatus = "bind";
       this.bindTemp = {
        id:row.id
      }
      this.BindDialogFormVisible = true;
    },
  
    bindSubmit() {
      this.$refs["bindForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          
         
          proxycarinfoBind({id:this.bindTemp.id,serialNo:this.bindTemp.carId})
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.getSelect();

                this.$message.success(res.msg);
                this.BindDialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });

        }
      })
    },
  
    getList() {
      let _t = this;
      _t.listLoading = true;
      proxycarinfoList({
       
      })
        .then(res => {
          if (res.status == 0) {
            this.listLoading = false;
            this.list = res.data;
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          _t.listLoading = false;
        });
    },
    //初始化通道数据
    getChannelNoOptions() {
      this.channelNoOptions = [];
      for (let i = 0; i < 17; i++) this.channelNoOptions.push({ channelNo: i });
    },
  
  

    //重置添加或编辑数据
    resetTemp() {
      this.temp = {
        
      };
    },
  
    //点击注册
    handleCreate() {
      this.resetTemp();
      this.dialogStatus = "create";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
   
    //注册
    createData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          proxycarinfoReg({imei:this.temp.imei})
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success(res.msg);
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
  
 
    //初始化下拉框
    getSelect() {
      var that = this;
    
       
         teamAndCarAllBindList().then(res => {
          if (res.status == 0) {
            if (res.data == null) {
              return;
            }
            let arr=[];
             for (let item in res.data) {
                 arr=arr.concat(res.data[item])
           
            }
            that.carOptions = [...arr]
          } else {
            if (res.data == null) {
              this.carOptions = [];
            
            }
            this.$message.error(res.msg);
          }
         
        });
        
     
    },
  
    scrollTop() {
      // 弹窗滚动到顶部
      let myDialog = document.querySelector("#myDialog .el-dialog__body");
      myDialog.scrollTop = 0;
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
 
  }
};
</script>

<style lang="scss">
#deviceProxy {
  .el-upload__tip{
    font-size: 14px;
    &.red{
        color:red;
        font-size: 16px;
    }
}
  #myDialog2 {
    img{
      width:100%;
    }
    .el-dialog {
      .el-dialog__body {
        display: flex;
        justify-content: center;
        margin: 0;
        padding-top: 20px;
        padding-bottom: 20px;
      }
    }
    .el-dialog__header {
      display: none;
    }
  }
  .el-button--primary.is-disabled {
    color: #fff;
    background-color: #a0cfff;
    border-color: #a0cfff;
  }
  .el-dialog {
    .el-input__inner::placeholder {
      font-size: 12px;
    }
  }
  .el-form-item__label {
    width: 86px;
  }

  .addAllForm {
    .el-form-item__label {
      width: 100px;
    }
    .el-form-item__content {
      margin-left: 100px;
    }
    .el-form-item__content {
      width: 293px;
    }
  }
  #myDialog{
     .el-form-item__label {
      width: 135px;
    }
    .el-form-item__content {
      margin-left: 135px;
    }
  }
  .editClass {
    .el-form-item__label {
      width: 135px;
    }
    .el-form-item__content {
      margin-left: 135px;
    }
  }
}
</style>
