import request from '../../request'

  //注册设备信息
  export function proxycarinfoReg(data) {
    return request({
      url: '/proxycarinfo/reg',
      method: 'post',
      data
      
    })
  }
  //绑定代理设备
  export function proxycarinfoBind(data) {
    return request({
      url: '/proxycarinfo/bind',
      method: 'post',
      data
      
    })
  }
  //注册列表    
  export function proxycarinfoList(data) {
    return request({
      url: '/proxycarinfo/list',
      method: 'post',
      data
      
    })
  }